class ReplacementList {
    public text: string;
    public replacements: string[];
    public current: string;
    public hot: boolean = false;
    public id: number;

    constructor(text: string, replacements: string[], id: number) {
        this.text = text;
        this.replacements = replacements;
        this.current = replacements[0];
        this.id = id;
    }
    public selectFrom(owner: ReplacementList[]) {
        owner.forEach((rl) => {rl.hot = false; });
        this.hot = true;
    }
}

export {ReplacementList};
