type RequestCallback = (data: string) => void;
type JSONCallback = (json: any) => void;

const simpleGet = (
    url: string,
    callback: RequestCallback,
    error: ((data: Event) => void) | undefined = undefined,
 ) => {
    const req = new XMLHttpRequest();
    if (error === undefined) {
        req.onerror = (e) => alert('it didin\'t work:\n' + e);
    } else {
        req.onerror = error;
    }
    req.onload = (e) => {
        if (req.status === 200) {
            callback(req.responseText);
        } else {
            (req.onerror as (e: Event) => void)(e);
        }
    };
    req.open('GET', url);
    req.send();
};

const getJSON = (
    url: string,
    callback: JSONCallback,
    error: ((data: Event) => void) | undefined = undefined,
) => {
    simpleGet(url, (text) => callback(JSON.parse(text)), error);
};

const getAPIData = (
    route: string,
    data: any,
    callback: JSONCallback,
    error: ((data: Event) => void) | undefined = undefined,
) => {
    getJSON(route + encodeURIComponent(data), callback, error);
};

const submit = (
    route: string,
    data: any,
    callback: RequestCallback,
    error: ((data: Event) => void) | undefined = undefined,
) => {
    simpleGet(route + encodeURIComponent(data), callback, error);
};

export {simpleGet, getJSON, getAPIData, submit};
