



















































































































import { Component, Vue, Prop } from 'vue-property-decorator';
import {ReplacementList} from '@/replacementlist';
import {applyQuery} from '@/nli_helpers';

function swap<T>(a: T[], i: number, j: number) {
    const temp = a[i];
    Vue.set(a, i, a[j]);
    Vue.set(a, j, temp);
}

@Component
export default class WordList extends Vue {
    // data
    @Prop() public words: ReplacementList[] = [];
    public applyQuery = applyQuery;

    // methods
    public select(el: ReplacementList) {
        el.selectFrom(this.words);
    }
    public toggle(el: ReplacementList) {
        if (el.hot) {
            el.hot = false;
        } else {
            el.selectFrom(this.words);
        }
    }
    public insert(el: ReplacementList, value= '', key= '') {
        let i = this.words.indexOf(el);
        const newEl = new ReplacementList(key, [value], this.words.length);
        this.words.splice( ++i, 0, newEl);
        newEl.selectFrom(this.words);
    }
    public remove(el: ReplacementList) {
        if (!window.confirm('really delete word: ' + el.current)) {
            return;
        }
        const i = this.words.indexOf(el);
        this.words.splice(i, 1);
    }
    public upOne(el: ReplacementList) {
        const i = this.words.indexOf(el);
        if (i < 1) {
            return 0;
        }
        swap(this.words, i - 1, i);
        return i - 1;
    }
    public downOne(el: ReplacementList) {
        const i = this.words.indexOf(el);
        if (i >= this.words.length - 1) {
            return this.words.length - 1;
        }
        swap(this.words, i + 1, i);
        return i + 1;
    }
    public boxSize(el: ReplacementList, ratio: number): string {
        return 'max-width:' + Math.max(el.current.length, 2) * ratio + 'rem';
    }
    public copyText(text: string) {
        const textbox =
            document.getElementById('converted-words') as HTMLInputElement;
        textbox.value = text;
        textbox.style.display = 'block';
        textbox.select();
        document.execCommand('copy');
        textbox.style.display = 'none';
    }
    // computed
    get original() {
        return this.words.map((rl) => rl.text).join(' ');
    }
    get converted() {
        return this.words.map((rl) => rl.current).join(' ');
    }
}

