

































































import { Component, Vue, Prop } from 'vue-property-decorator';
import WordList from '@/components/WordList.vue'; // @ is an alias to /src
import { ReplacementList } from '@/replacementlist';
import {simpleGet, getJSON, getAPIData, submit} from '@/api_requests';
import {
  Dictionary,
  Subfields,
  Fields,
  MARCRecord,
  iterRecord,
  composeTitle,
  getNLIName,
  getNLIPublisherInfo,
  getNLIDateOfControl,
  getNLITitle,
  getNLIType,
  getPicaTitle,
  getPicaName,
  getPicaPublicationInfo,
  getPicaPhysicalForm,
  applyQuery,
} from '@/nli_helpers';

const addWords = (recieved: any[]) => {
    const rlists: ReplacementList[] = [];
    for (const rl of recieved) {
        rlists.push(
            new ReplacementList(
                rl.key, rl.reps, rlists.length,
            ),
        );
    }
    return rlists;
};

@Component({
  watch: {$route: 'fetchData'},
  components: {
    WordList,
  },
})

export default class PPNView extends Vue {
  public words: ReplacementList[] = [];
  public record: Fields = {};
  public title: string = '';
  public showRecord: boolean = false;
  public NLIResults: MARCRecord[] = [];
  public applyQuery = applyQuery;

  public goNext() { this.$router.push({path: '/next/'}); }
  public loadNext(e: Event) { this.goNext(); }
  public queryNLIdb() {
    const mapper = (rec: any) => {
      const controlfields = rec.controlfields;
      delete rec.controlfields;
      return {['controlfields']: controlfields, fields: rec, display: false};
    };
    const callback = (results: any[]) => this.NLIResults = results.map(mapper);
    getAPIData('/nli/', JSON.stringify(this.current), callback);
  }
  public fetchData() {
    getAPIData('/ppn/', this.$route.params.ppn, (resp) => {
      for (const rlist of resp.replists) {
        rlist.reps = rlist.reps.slice(0, 7);
      }
      this.words = addWords(resp.replists);
      this.record = resp.record;
      this.title = this.getTitle();
      this.queryNLIdb();
    },
    this.loadNext);
  }
  public submitMatch(nlidoc: Fields, full= false) {
    const submittedData = {
      ppn: this.$route.params.ppn,
      corrected: [],
      match: nlidoc,
      textonly: !full,
    };
    submit('/submit/', JSON.stringify(submittedData),
    (ppn) => this.$router.push({path: '/ppn/' + ppn}),
    this.loadNext);
  }
  public submitUpdate() {
    const submittedData = {ppn: this.$route.params.ppn, corrected: this.words, match: {}};
    submit('/submit/', JSON.stringify(submittedData),
    (ppn) => this.$router.push({path: '/ppn/' + ppn}),
    this.loadNext);
  }
  public pass() {
    simpleGet('/pass/' + this.$route.params.ppn,
    (ppn) => this.$router.push({path: '/ppn/' + ppn}),
    this.loadNext);
  }
  public created() { this.fetchData(); }
  public iterRecord(rec: Fields) { return iterRecord(rec); }
  public get current() { return this.words.map((rl) => rl.current); }
  public getTitle() { return getPicaTitle(this.record); }
  public getName() { return getPicaName(this.record); }
  public getPublicationInfo() { return getPicaPublicationInfo(this.record); }
  public getPhysicalForm() { return getPicaPhysicalForm(this.record); }

  public getNLITitle(record: Fields) { return getNLITitle(record); }
  public getNLIName(record: Fields) { return getNLIName(record); }
  public getNLIPublisherInfo(record: Fields) { return getNLIPublisherInfo(record); }
  public getNLIDateOfControl(controlfields: Dictionary<string>) { return getNLIDateOfControl(controlfields); }
  public getNLIType(controlfields: Dictionary<string>) { return getNLIType(controlfields); }
}
