interface Dictionary<T> {[key: string]: T; }
interface Subfields extends Dictionary<string[]> {}
interface Fields extends Dictionary<Subfields[]> {}
interface MARCRecord {
  fields: Fields;
  controlfields: Dictionary<string>;
  display: boolean;
}

interface FlatSubfield { name: string; val: string; }
interface FlatField { name: string; val: FlatSubfield[]; }

const iterRecord = (record: Fields) => {
  const out: FlatField[] = [];
  for (const [fname, fields] of Object.entries(record)) {
    for (const field of fields as any) {
      const tojoin: FlatSubfield[] = [];
      for (const [subname, subs] of Object.entries(field)) {
        for (const sub of subs as string[]) {
          tojoin.push({name: subname, val: sub});
        }
      }
      out.push({name: fname, val: tojoin});
    }
  }
  return out;
};

const cleanNLITitle = (title: string) => {
  const end = title[title.length - 1];
  if (end === ':' || end === '/') {
    title = title.slice(0, title.length - 2);
  }
  return title.replace(/<<(.*?)>> ?/, '$1 @');
};

const composeTitle = (main: string[], subtitle: string[] | undefined, responsibility: string[] | undefined) => {
  const out: string[] = cleanNLITitle(main[0]).split(/\s+/);
  if (subtitle !== undefined) {
    out.push(':');
    out.push(...cleanNLITitle(subtitle[0]).split(/\s+/));
  }
  if (responsibility !== undefined) {
    out.push('/');
    out.push(...cleanNLITitle(responsibility[0]).split(/\s+/));
  }
  return out;
};

function getF<T>(record: Dictionary<T[]> | undefined, fn: string) {
  if (record === undefined) {
    return undefined;
  }
  const fields = record[fn];
  if (fields === undefined) {
    return undefined;
  }
  return fields[0];
}

const getSub = (record: Fields, fieldname: string, subname: string) => {
  const field = getF(record, fieldname);
  const subfield = getF(field, subname);
  return subfield;
};

const getNLITitle = (record: Fields) => {
  const title = record['245'][0];
  return composeTitle(title.a, title.b, title.c).join(' ');
};

const getNLIName = (record: Fields) => {
  for (const fieldname of ['100', '400', '700']) {
    const val = getSub(record, fieldname, 'a');
    if (val !== undefined) {
      return val;
    }
  }
  return '(no name)';
};

const getNLIPublisherInfo = (record: Fields) =>
  ['a', 'b', 'c']
    .map((subname) => getSub(record, '260', subname))
    .filter((val) => val !== undefined)
    .join(' ');

const getNLIDateOfControl = (controlfields: Dictionary<string>) => {
  const val = controlfields['008'];
  if (val === undefined) {
    return undefined;
  }
  return val.slice(7, 11);
};

const nliTypemap: Dictionary<string> = {
  '#': 'Not encodable',
  'a': 'Microfilm',
  'b': 'Microfiche',
  'c': 'Microopaque',
  'd': 'Large print',
  'f': 'Braille',
  'o': 'Online',
  'q': 'Direct electronic',
  's': 'Electronic',
};

const getNLIType = (controlfields: Dictionary<string>) => {
  const val = controlfields['008'];
  if (val === undefined) {
    return undefined;
  }
  return nliTypemap[val[23]];
};

const getPicaTitle = (record: Fields) => {
  const title = record['021A'][0];
  return composeTitle(title.a, title.d, title.h).join(' ');
};

const getPicaName = (record: Fields) => {
  for (const fieldname of Object.keys(record)) {
    if (fieldname.startsWith('028')) {
      const subs = record[fieldname][0];
      if (subs.a !== undefined && subs.d !== undefined) {
        return subs.a[0] + ', ' + subs.d[0];
      } else if (subs['8'] !== undefined) {
        return subs['8'][0].split('$Z')[0];
      }
    }
  }
  return '(no name)';
};

const getPicaPublicationInfo = (record: Fields) => {
  let place = getSub(record, '033A', 'p');
  let publisher = getSub(record, '033A', 'n');
  if (place === undefined && publisher === undefined) {
    place = getSub(record, '033C', 'p');
    publisher = getSub(record, '033C', 'n');
  }
  let date = getSub(record, '011@', 'a');
  if (date === undefined) {
    date = '(date unknown)';
  }
  return place + ': ' + publisher + ', ' + date;
};
const picaPhysical: Dictionary<string> = {
  A: 'Druckschriften',
  B: 'Audiovisuelles Material, nicht digital',
  C: 'Blindenschriftträger',
  D: 'entfällt!! (aufgegangen in "H")',
  E: 'Mikroformen',
  G: 'Tonträger',
  H: 'Handschriftliches Material',
  I: 'Illustrationen',
  K: 'Kartenmaterial, gedruckt',
  L: 'Lokalkatalogisat*',
  M: 'Notendrucke',
  O: 'Online-Ressourcen',
  S: 'Elektronische Ressourcen auf Datenträgern',
  V: 'Objekte',
  Z: 'multimedial',
};
const picaBibliographic: Dictionary<string> = {
  a: 'einzelne Einheit, nicht Teil einer mehrteiligen Monografie',
  b: 'Zeitschrift/Zeitung/integrierende Online-Ressource aus der ZDB',
  c: 'Übergeordnete Aufnahme einer mehrteiligen Monografie',
  d: 'Gesamttitelaufnahme einer monografischen Reihe (auch aus der ZDB)',
  f: 'Teil mit abhängigem Titel einer mehrteiligen Monografie',
  F: 'Teil mit unabhängigem Titel einer mehrteiligen Monografie',
  o: 'Unselbständig erschienene Teile von Zusammenstellungen (Artikel, Aufsätze)',
  z: 'Datensatz, der ausschließlich für Erwerbungszwecke genutzt wird (Fortsetzungsbestellung)',
};
const getPicaPhysicalForm = (record: Fields) => {
  const type = getSub(record, '002@', '0');
  if (type === undefined || type.slice(0, 2) === 'Aa') {
    return undefined;
  }
  return picaPhysical[type[0]] + ', ' + picaBibliographic[type[1]];
};

const queryURLs: Dictionary<string> = {
    morfix: 'http://www.morfix.co.il/QUERY_HERE',
    googleTranslate: 'https://translate.google.com/#iw/en/QUERY_HERE',
    nli: 'http://merhav.nli.org.il/primo_library/libweb/action/search.do;jsessionid=DD40D325955438DF28DC1C47A7B81B2C?fn=search&ct=search&initialSearch=true&mode=Basic&tab=default_tab&indx=1&dum=true&srt=rank&vid=NLI&frbg=&vl%28freeText0%29=QUERY_HERE&scp.scps=scope%3A%28NNL%29',
    kvk: 'http://kvk.bibliothek.kit.edu/hylib-bin/kvk/nph-kvk2.cgi?maske=kvk-redesign&lang=de&title=KIT-Bibliothek%3A+Karlsruher+Virtueller+Katalog+KVK+%3A+Ergebnisanzeige&head=%2F%2Fkvk.bibliothek.kit.edu%2Fasset%2Fhtml%2Fhead.html&header=%2F%2Fkvk.bibliothek.kit.edu%2Fasset%2Fhtml%2Fheader.html&spacer=%2F%2Fkvk.bibliothek.kit.edu%2Fasset%2Fhtml%2Fspacer.html&footer=%2F%2Fkvk.bibliothek.kit.edu%2Fasset%2Fhtml%2Ffooter.html&css=none&input-charset=utf-8&ALL=&TI=QUERY_HERE&AU=&CI=&ST=&PY=&SB=&SS=&PU=&kataloge=COPAC&kataloge=VERBUND_ISRAEL&kataloge=WORLDCAT&ref=direct&client-js=yes',
    google: 'https://google.com/search?q=QUERY_HERE',
    marc21: 'https://www.loc.gov/marc/bibliographic/concise/bdQUERY_HERE.html',
    hebis: 'http://cbsopac.rz.uni-frankfurt.de/DB=2.1/PPNSET?PPN=QUERY_HERE',
};

const applyQuery = (template: string, query: string) => {
    const encoded = encodeURIComponent(query);
    return queryURLs[template].replace('QUERY_HERE', encoded);
};

export {
  Dictionary,
  FlatField,
  FlatSubfield,
  Fields,
  Subfields,
  MARCRecord,
  iterRecord,
  cleanNLITitle,
  composeTitle,
  getNLIName,
  getNLIPublisherInfo,
  getNLIDateOfControl,
  getNLITitle,
  getNLIType,
  getPicaTitle,
  getPicaName,
  getPicaPublicationInfo,
  getPicaPhysicalForm,
  applyQuery,
};
