import Vue from 'vue';
import Router from 'vue-router';
import PPNView from './views/PPNView.vue';
import {simpleGet, getJSON, getAPIData, submit} from '@/api_requests';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/next',
    },
    {
      path: '/ppn/:ppn',
      name: 'ppn',
      component: PPNView,
    },
    {
      path: '/next',
      redirect: () => {
        const request = new XMLHttpRequest();
        request.open('GET', '/next/', false);
        request.send(null);
        if (request.status === 200) {
          return '/ppn/' + request.responseText;
        } else {
          return '/next/';
        }
      },
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import('./views/About.vue'),
    },
  ],
});
